import React from "react";
import { connect } from "react-redux";

import { setPage } from "../../store/router/actions";
import { setActiveTab, setScrollPositionByID } from "../../store/vk/actions";
import { restoreScrollPosition } from "../../services/_functions";

import {
  Div,
  Panel,
  Group,
  Header,
  CellButton,
  PanelHeader,
  FixedLayout,
  HorizontalScroll,
  TabsItem,
  Tabs,
  Search,
  List,
  Cell,
  Button
} from "@vkontakte/vkui";

class HomePanelProfile extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeTab: props.activeTab["EXAMPLE"] || "modal",
      search: null,
      //Students List
      students: null
    };
    //
    this.onChangeSearch = this.onChangeSearch.bind(this);
  }

  setTab(tab) {
    this.setState({
      activeTab: tab,
    });
  }

  componentWillUnmount() {
    const { setScrollPositionByID, setActiveTab } = this.props;

    setActiveTab("EXAMPLE", this.state.activeTab);
    setScrollPositionByID("EXAMPLE_TABS_LIST");
  }

  componentDidMount() {
    restoreScrollPosition();
    //get Data
    this.getStudents();
  }

  onChangeSearch(e) {
    if (e.target.value && e.target.value.length > null) {
      //   this.state.search = e.target.value;
      this.setState({ search: e.target.value });


      this.setState({activeTab: "search"});
      
      console.log(e.target.value);
    } else {
        this.setState({activeTab: "home"});
        //
        this.setState({search: ''});
      console.debug("[Search] empty value");
    }
    //this.setState({ search: e.target.value });
  }

  onBlurSearch(e) {
    console.log(e);
    console.log("Вы вышли за поле зрения поиска!");
  }

  getStudents(){
    fetch("https://gorest.co.in/public-api/users")
    .then(res => res.json())
    .then(
      (result) => {
        this.setState({
          isLoaded: true,
          students: result.data
        });
      },
      // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
      // чтобы не перехватывать исключения из ошибок в самих компонентах.
      (error) => {
        this.setState({
          isLoaded: true,
          error
        });
      }
    )
  }


  render() {
    //
    const { id, setPage } = this.props;

    //
    const boxStyle = { marginTop: 100 };

    return (
      <Panel id={id}>
        {/* <PanelHeader noShadow={true}>Examples 2</PanelHeader> */}
        <FixedLayout vertical="top">
          {/* <Tabs theme="header" mode="default"> */}
          <Tabs>
            {test.map((item) => (
              <TabsItem
                onClick={() => {
                  if (this.state.activeTab === item.slug) {
                    this.setState({ contextOpened: !this.state.contextOpened });
                  }
                  this.setState({ activeTab: item.slug });
                }}
                selected={this.state.activeTab === item.slug}
              >
                {item.title}
              </TabsItem>
            ))}
          </Tabs>

          <Search
            value={this.state.search}
            onChange={this.onChangeSearch}
            onBlur={this.onBlurSearch}
            // value={this.state.search}
          />
        </FixedLayout>

        <Group style={boxStyle}>


            {/* Home */}
          {this.state.activeTab !== "search" && (
            <Div>

            {/* <h1>Типо вьюха</h1> */}



            {/* Текущие состояние активного таба */}
            

            {/* <ul>
                {this.state.students !== null ?  
                    this.state.students.map(item => (
                        <li>{JSON.stringify(item)}</li>
                        )) 
                : null}
            </ul> */}

            {/* Список студентов */}
            {this.state.students !== null &&
            <Group header={<Header mode="secondary">Список {this.state.activeTab}</Header>}>
                <List>
                  {this.state.students.map((item, index) => (

                    <Cell 
                    key={item}
                    size="l"
                    bottomContent={
                    <div style={{ display: 'flex' }}>
                      <Button>Отметиться</Button>
                    </div>
                    }
                    >{JSON.stringify(item.name)}</Cell>

                  ))}
                </List>
              </Group>
            }

            </Div>
          )}



          {this.state.activeTab == "search" && (
            <Div>
            <h1>Типо вьюха</h1>
            {/* Текущие состояние активного таба */}
            {this.state.activeTab}
            {`\n`}
            {`\n`}
            Поиск данных

            </Div>
          )}




        {/* Отладка состояния */}
          {JSON.stringify(this.state)}
        </Group>


      </Panel>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    activeTab: state.vkui.activeTab,
  };
};

const mapDispatchToProps = {
  setPage,
  setActiveTab,
  setScrollPositionByID,
};


let test = [
  {
    id: 0,
    title: "ССА",
    slug: "ssa",
  },
  {
    id: 1,
    title: "ПКС",
    slug: "pks",
  },
  {
    id: 2,
    title: "ИКС",
    slug: "iks",
  },
  {
    id: 3,
    title: "ПС",
    slug: "ps",
  },
  {
    id: 4,
    title: "Р1",
    slug: "r1",
  },
  {
    id: 5,
    title: "Р1К",
    slug: "r1k",
  },
  {
    id: 6,
    title: "12",
    slug: "gr12",
  },
  {
    id: 7,
    title: "13",
    slug: "gr13",
  },
];

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);
